.reviewes {
  height: 100vh;
  background-color: #1e1e1e;
  &__title {
    text-transform: uppercase;
    text-align: center;
    position: relative;
    color: #fff;
    font-family: "Inter";
    padding-top: 100px;
    margin: 0;
    @media (min-width: 992px) {
      text-align: left;
      font-size: 40px;
    }
    &::after {
      content: "";
      position: absolute;
      bottom: -10px;
      left: 0;
      right: 0;
      height: 2px;
      background: linear-gradient(
        132.77deg,
        #e0d2ff 7.48%,
        #efc4fe 27%,
        #e9fffb 58.5%,
        #b7b2f2 92.67%
      );
      @media (min-width: 992px) {
        bottom: 10px;
        left: 250px;
      }
    }
  }
}
.card {
  width: 200px;
  color: #000;
  // text-align: center;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  border-radius: 35px;
  background: #fff;
  box-shadow: 7px 9px 35px 11px rgba(224, 210, 255, 0.25) inset,
    -2px 5px 35px 5px rgba(0, 0, 0, 0.5);
  padding: 10px 32px 50px 32px;
}
.reviews__text {
  text-align: center;
}
.swiper-slide-reviews {
  width: 200px;
  height: 100%;
  position: relative;
  transition-property: transform;
  margin-top: 70px;
  margin-left: 20px;
}
.swiper-slide-invisible-blank {
  visibility: hidden;
}
.swiper-autoheight {
  &,
  .swiper-slide-reviews {
    height: auto;
  }

  .swiper-wrapper {
    align-items: flex-start;
    transition-property: transform, height;
  }
}
.button-next,
.button-prev {
  z-index: 1000;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 20px;
  bottom: 60%;
}
.button-next {
  left: 50%;
}
.button-prev {
  transform: rotate(180deg);
  right: 50%;
  margin-right: 10px;
}
